#app4 .circle {
    width: 15vw;
    height: 15vw;
    border: 1px solid red;
    border-radius: 50%;
}

#app4 .circle.active {
    animation: change 1s infinite linear alternate;
}

@keyframes change {
    0% {
        background: red;
    }
    100% {
        background: blue;
    }
}