#app2 .tab-bar {
    display: flex;
}

#app2 .tab-bar > li {
    width: 50%;
    border: 1px solid #000000;
}

#app2 .tab-bar > li.selected {
    background: rgb(253, 221, 155);
}

#app2 .tab-content > li {
    display: none;
}

#app2 .tab-content > li.active {
    display: block;
}